/* div에 스타일 적용 */
.selectDiv {
    border: 1px solid #ccc; /* 회색 외곽선 */
    padding: 10px; /* 내용과 외곽선 사이 간격 */
    background-color: #f2f2f2; /* 배경색 설정 */
    width: 49%;
    max-width: 100%;
}

/* 테이블 컨테이너 */
.playerTable {
    border: 1px solid #ccc; /* 외곽선 추가 */
    border-collapse: collapse; /* 셀 경계 통합 */
    width: 50%; /* 테이블 컨테이너 너비를 100%로 설정 */
    max-width: 100%; /* 최대 너비를 100%로 설정 */
}

/* 테이블 헤더와 셀 스타일 */
.playerTable th, .playerTable td {
    border: 1px solid #ccc; /* 외곽선 추가 */
    padding: 10px; /* 셀 내용과 외곽선 사이 간격 */
    text-align: center; /* 텍스트 중앙 정렬 */
    font-weight: bold; /* 볼드체로 표시 */
}

/* 불필요한 셀 내용 여백 제거 */
.playerTable td:first-child, .playerTable th:first-child {
    padding-left: 0;
}

/* 배경색 및 텍스트 색상 설정 */
.playerTable th {
    background-color: #f2f2f2; /* 배경색 설정 */
    color: #333; /* 텍스트 색상 설정 */
}


.player-list-container {
    float: left; /* 리스트 컨테이너를 왼쪽으로 띄웁니다. */
    width: 70%; /* 리스트 컨테이너의 폭을 조절합니다. */
    padding: 20px;
}

.modal {
    display: block; /* 팝업 모달을 표시합니다. */
    position: absolute;
    top: 20px; /* 원하는 위치로 조절하세요. */
    right: 20px; /* 원하는 위치로 조절하세요. */
    background-color: white;
    border: 1px solid #ccc;
    padding: 20px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
}

